import React from "react";
import ParentForm from "../../../common/form";
import Joi from "joi-browser";
import {
  Card,
  CardBody,
  CardFooter,
  Form,
  Row,
  Col,
} from "reactstrap";

class TrainingAdd extends ParentForm {
  constructor(props) {
    super(props);
    this.initialState = {
      data: {
        id: "",
        employee_number: "",
        employee_name: "",
        department: "",
        disciplinary_action_type: "",
        from_date: "",
        to_date: "",
        remarks: "",
      },
      errors: {},
    };
    this.state = this.initialState;
    this.schema = {
      id: Joi.string().allow("").optional(),
      employee_number: Joi.string()
        .optional()
        .allow("")
        .label("Employee Number"),
      department: Joi.string().optional().allow("").label("Department"),
      employee_name: Joi.string().optional().allow("").label("Employee Name"),
      disciplinary_action_type: Joi.string()
        .required()
        .label("Disciplinary Action Type"),
      from_date: Joi.string().required().label("From Date"),
      to_date: Joi.string().required().label("To Date"),
      remarks: Joi.string().allow("").optional(),
    };
  }

  populateState(data) {
    const updatedState = {
      ...this.state,
      data: {
        id: data.id ? data.id : "",
        employee_number: data.employee_number ? data.employee_number : "",
        employee_name: data.employee_name ? data.employee_name : "",
        department: data.department ? data.department : "",
        disciplinary_action_type: data.disciplinary_action_type
          ? data.disciplinary_action_type
          : "",
        from_date: data.from_date ? data.from_date : "",
        to_date: data.to_date ? data.to_date : "",
        remarks: data.remarks ? data.remarks : "",
      },
      lockUpdate: true,
    };
    this.setState(updatedState);
  }

  componentDidUpdate() {
    if ((this.props.isEdit || this.props.isView) && !this.state.lockUpdate) {
      this.populateState(this.props.data);
    }
  }

  doSubmit() {
    this.props.submit(this.state.data);
  }

  render() {
    return (
      <Card className="border-0">
        <CardBody>
          <Form onSubmit={this.handleSubmit}>
            <Row>
              <Col md={4} sm={12} xs={12}>
                {this.renderSelect({
                  name: "disciplinary_action_type",
                  label: "Disciplinary Action Tpe",
                  options: [
                    "Disciplinary Action Type 1",
                    "Disciplinary Action Type 2",
                  ],
                })}
              </Col>
              <Col md={4} sm={12} xs={12}>
                {this.renderInput({
                  name: "from_date",
                  label: "From Date",
                  type: "date",
                })}
              </Col>
              <Col md={4} sm={12} xs={12}>
                {this.renderInput({
                  name: "to_date",
                  label: "To Date",
                  type: "date",
                })}
              </Col>

              <Col md={12} sm={12} xs={12}>
                {this.renderInput({
                  name: "remarks",
                  label: "Remarks",
                  type: "textarea",
                })}
              </Col>
            </Row>
            <CardFooter align="center">{this.renderButton("Save")}</CardFooter>
          </Form>
        </CardBody>
      </Card>
    );
  }
}
export default TrainingAdd;
