import React from "react";
import ParentForm from "../../../common/form";
import Joi from "joi-browser";
import {
  Card,
  CardBody,
  CardHeader,
  CardFooter,
  Form,
  Row,
  Col,
} from "reactstrap";

class QualificationAdd extends ParentForm {
  constructor(props) {
    super(props);
    this.initialState = {
      data: {
        employee: "",
        qualification_level_type: "",
        education_type: "",
        institute: "",
        start_date: "",
        end_date: "",
        duration: "",
        document_file: "",
        remarks: "",
      },
      errors: {},
    };
    this.state = this.initialState;
    this.schema = {
      employee: Joi.string().allow("").optional(),
      qualification_level_type: Joi.string()
        .required()
        .label("qualification_level_type"),
      institute: Joi.string().required().label("institute"),
      start_date: Joi.string().required().label("start_date"),
      end_date: Joi.string().required().label("end_date"),
      duration: Joi.string().required().label("duration"),
      document_file: Joi.string(),
      remarks: Joi.string().allow("").optional(),
    };
  }
  populateState(qualification) {
    const updatedState = {
      ...this.state,
      data: {
        id: qualification.id,
        employee: qualification.employee.id,
        qualification_level_type: qualification.qualification_level_type.id,
        education_type: qualification.education_type.id,
        institute: qualification.institute.id,
        start_date: qualification.start_date,
        end_date: qualification.end_date,
        duration: qualification.start_date + qualification.end_date,
        document_file: qualification.document_file,
        remarks: qualification.remarks,
      },
      lockUpdate: true,
    };
    this.setState(updatedState);
  }

  componentDidUpdate() {
    if ((this.props.isEdit || this.props.isView) && !this.state.lockUpdate) {
      this.populateState(this.props.data);
    }
  }

  doSubmit() {
    const body = {
      ...this.state.data,
      is_blocked: this.state.data.is_blocked ? true : false,
    };
    this.props.submit(body);
  }

  render() {
    const { isView } = this.props;
    return (
      <Card className="border-0">
        <CardHeader className="border-0">New Qualification</CardHeader>
        <CardBody>
          <Form>
            <Row>
              <Col md={3} sm={12} xs={12}>
                {this.renderSelect(
                  "qualification_level_type",
                  "Qualification Level Type ",
                  ["level type 1", "level type 2"]
                )}
              </Col>
              <Col md={3} sm={12} xs={12}>
                {this.renderSelect("education_type", "Education Type ", [
                  "type 1",
                  "type 2",
                ])}
              </Col>
              <Col md={3} sm={12} xs={12}>
                {this.renderSelect("institute", "Institute", [
                  "institute  1",
                  "institute  2",
                ])}
              </Col>

              <Col md={3} sm={12} xs={12}>
                {this.renderInput("start_date", "Start Date", "Date")}
              </Col>
              <Col md={3} sm={12} xs={12}>
                {this.renderInput("end_date", "End Date", "Date")}
              </Col>
              {isView && (
                <Col md={3} sm={12} xs={12}>
                  {this.renderInput("duration", "Duration")}
                </Col>
              )}
              <Col md={3} sm={12} xs={12}>
                {this.renderInput("document_file", "Document File")}
              </Col>

              <Col md={12} sm={12} xs={12}>
                {this.renderInput("remarks", "Remarks", "textarea")}
              </Col>
            </Row>
          </Form>
        </CardBody>
        <CardFooter align="center">
          {this.renderButton("save Qualification")}
        </CardFooter>
      </Card>
    );
  }
}
export default QualificationAdd;
