import React, { useReducer, useEffect } from "react";
import Page from "../../../../components/Page";
import { Card, CardBody, Button, Col } from "reactstrap";
import CommonModals from "../../../../components/CommonModal";
import PerformanceAdd from "./PerformanceAdd";
import { MdRemoveRedEye, MdEdit, MdDelete } from "react-icons/md";
import { reverse } from "../../../../helpers/reverse";
import CustomTable from "../../../common/table";
import { _toggle, initialState, reducer } from "../../../common/ModalOptions";
import { getDateFormat } from "../../../../helpers/date";

const Performance = ({
  doneAdd,
  addPerformance,
  doneEdit,
  editPerformance,
  deletePerformance,
  performances,
}) => {
  const [state, dispatch] = useReducer(reducer, initialState);
  const columns = [
    { path: "employee_number", label: "Employee Number" },
    { path: "goal_type", label: "Goal Type" },
    { path: "evolution_result", label: "Evolution Result" },
    { path: "evaluation_date", label: "Evaluation Date" },
    { path: "updated_at", label: "Last Modified Date" },
    { path: "remarks", label: "Remarks" },

    {
      key: "view",
      content: (performances) => (
        <>
          <Button
            className="m-1"
            size="sm"
            outline
            color="info"
            onClick={() => {
              _toggle(
                {
                  type: "VIEW",
                  Component: PerformanceAdd,
                  data: performances,
                  title: "View Performance",
                },
                dispatch
              );
            }}
          >
            <MdRemoveRedEye />
          </Button>
          <Button
            className="m-1"
            size="sm"
            outline
            color="warning"
            onClick={() => {
              _toggle(
                {
                  type: "EDIT",
                  Component: PerformanceAdd,
                  submit: editPerformance,
                  data: performances,
                  title: "Edit Performance",
                },
                dispatch
              );
            }}
          >
            <MdEdit />
          </Button>
          <Button
            className="m-1"
            size="sm"
            outline
            color="danger"
            onClick={() => {
              _toggle(
                {
                  type: "DELETE",
                  deleteOptions: {
                    okCallback: okDelete,
                    title: "Are you sure?",
                    id: performances.id,
                    message: "",
                  },
                },
                dispatch
              );
            }}
          >
            <MdDelete />
          </Button>
        </>
      ),
    },
  ];

  useEffect(() => {
    if (doneAdd || doneEdit) {
      _toggle({ type: "CLOSE" }, dispatch);
    }
  }, [doneAdd, doneEdit]);

  const okDelete = (id) => {
    deletePerformance(id);
  };

  return (
    <div>
      <Page>
        <CommonModals
          data={state.data}
          openModal={state.openModal}
          component={state.Component}
          toggle={_toggle}
          dispatch={dispatch}
          title={state.title}
        />
        <Col align="right" className="mb-1 pl-3 pr-3">
          <Button
            onClick={() =>
              _toggle(
                {
                  type: "ADD",
                  Component: PerformanceAdd,
                  submit: addPerformance,
                  title: "New Performance",
                },
                dispatch
              )
            }
            outline
            size="sm"
          >
            New Performance
          </Button>
        </Col>
        <Card className="border-0">
          <CardBody>
            <CustomTable
              title="Performance"
              columns={columns}
              data={reverse(
                performances.map((item) => ({
                  ...item,
                  updated_at: getDateFormat(item.updated_at),
                }))
              )}
            />
          </CardBody>
        </Card>
      </Page>
    </div>
  );
};

export default Performance;
