import React, { useReducer, useEffect } from "react";
import Page from "../../../../components/Page";
import { Card, CardBody, Button, Col } from "reactstrap";
import CommonModals from "../../../../components/CommonModal";
import AbsenceRegistrationAdd from "./AbsencesRegistrationAdd";
import { MdRemoveRedEye, MdEdit, MdDelete } from "react-icons/md";
import { reverse } from "../../../../helpers/reverse";
import CustomTable from "../../../common/table";
import { _toggle, initialState, reducer } from "../../../common/ModalOptions";
import { getDateFormat } from "../../../../helpers/date";

const AbsenceRegistration = ({
  doneAdd,
  addAbsenceRegistration,
  doneEdit,
  editAbsenceRegistration,
  deleteAbsenceRegistration,
  absenceRegistrations,
}) => {
  const [state, dispatch] = useReducer(reducer, initialState);
  const columns = [
    { path: "employee_number", label: "Employee Number" },
    { path: "start_date", label: "Start Date" },
    { path: "end_date", label: "End Date" },
    { path: "total_absence_days", label: "Total Absence Days" },
    { path: "total_absence_hours", label: "Total Absence Hours" },
    { path: "total_absence", label: "Total Absence" },
    { path: "updated_at", label: "Last Modified Date" },
    { path: "remarks", label: "Remarks" },

    {
      key: "view",
      content: (absenceRegistrations) => (
        <>
          <Button
            className="m-1"
            size="sm"
            outline
            color="info"
            onClick={() => {
              _toggle(
                {
                  type: "VIEW",
                  Component: AbsenceRegistrationAdd,
                  data: absenceRegistrations,
                  title: "View Absence Registration",
                },
                dispatch
              );
            }}
          >
            <MdRemoveRedEye />
          </Button>
          <Button
            className="m-1"
            size="sm"
            outline
            color="warning"
            onClick={() => {
              _toggle(
                {
                  type: "EDIT",
                  Component: AbsenceRegistrationAdd,
                  submit: editAbsenceRegistration,
                  data: absenceRegistrations,
                  title: "Edit Absence Registration",
                },
                dispatch
              );
            }}
          >
            <MdEdit />
          </Button>
          <Button
            className="m-1"
            size="sm"
            outline
            color="danger"
            onClick={() => {
              _toggle(
                {
                  type: "DELETE",
                  deleteOptions: {
                    okCallback: okDelete,
                    title: "Are you sure?",
                    id: absenceRegistrations.id,
                    message: "",
                  },
                },
                dispatch
              );
            }}
          >
            <MdDelete />
          </Button>
        </>
      ),
    },
  ];

  useEffect(() => {
    if (doneAdd || doneEdit) {
      _toggle({ type: "CLOSE" }, dispatch);
    }
  }, [doneAdd, doneEdit]);

  const okDelete = (id) => {
    deleteAbsenceRegistration(id);
  };

  return (
    <div>
      <Page>
        <CommonModals
          data={state.data}
          openModal={state.openModal}
          component={state.Component}
          toggle={_toggle}
          dispatch={dispatch}
          title={state.title}
        />
        <Col align="right" className="mb-1 pl-3 pr-3">
          <Button
            onClick={() =>
              _toggle(
                {
                  type: "ADD",
                  Component: AbsenceRegistrationAdd,
                  submit: addAbsenceRegistration,
                  title: "New Absence Registration",
                },
                dispatch
              )
            }
            outline
            size="sm"
          >
            New Absence Registration
          </Button>
        </Col>
        <Card className="border-0">
          <CardBody>
            <CustomTable
              title="Absence Registration"
              columns={columns}
              data={reverse(
                absenceRegistrations.map((item) => ({
                  ...item,
                  updated_at: getDateFormat(item.updated_at),
                }))
              )}
            />
          </CardBody>
        </Card>
      </Page>
    </div>
  );
};

export default AbsenceRegistration;
