import React from "react";
import ParentForm from "../../../common/form";
import Joi from "joi-browser";
import {
  Card,
  CardBody,
  CardFooter,
  Form,
  Row,
  Col,
} from "reactstrap";

class LeaveBalanceAdd extends ParentForm {
  constructor(props) {
    super(props);
    this.initialState = {
      data: {
        id: "",
        employee_number: "",
        annual_leave: "",
        leave_type: "",
        leave_request_date: "",
        leave_from_date: "",
        leave_to_date: "",
        leave_balance: "",
        remarks: "",
      },
      errors: {},
    };
    this.state = this.initialState;
    this.schema = {
      id: Joi.string().allow("").optional(),
      employee_number: Joi.string()
        .optional()
        .allow("")
        .label("Employee Number"),
      annual_leave: Joi.string().required().label("Annual Leave"),
      leave_type: Joi.string().required().label("Leave Type"),
      leave_from_date: Joi.string().required().label("Leave from Date"),
      leave_to_date: Joi.string().required().label("Leave to Date"),
      leave_request_date: Joi.string().required().label("Leave Request Date"),
      leave_balance: Joi.string().required().label("Leave Balance"),

      remarks: Joi.string().allow("").optional(),
    };
  }

  populateState(data) {
    const updatedState = {
      ...this.state,
      data: {
        id: data.id ? data.id : "",
        employee_number: data.employee_number ? data.employee_number : "",
        annual_leave: data.annual_leave ? data.annual_leave : "",
        leave_type: data.leave_type ? data.leave_type : "",
        leave_request_date: data.leave_request_date
          ? data.leave_request_date
          : "",
        leave_from_date: data.leave_from_date ? data.leave_from_date : "",
        leave_to_date: data.leave_to_date ? data.leave_to_date : "",
        leave_balance: data.leave_balance ? data.leave_balance : "",
        remarks: data.remarks ? data.remarks : "",
      },
      lockUpdate: true,
    };
    this.setState(updatedState);
  }

  componentDidUpdate() {
    if ((this.props.isEdit || this.props.isView) && !this.state.lockUpdate) {
      this.populateState(this.props.data);
    }
  }

  doSubmit() {
    this.props.submit(this.state.data);
  }

  render() {
    return (
      <Card className="border-0">
        <CardBody>
          <Form onSubmit={this.handleSubmit}>
            <Row>
              <Col md={6} sm={12} xs={12}>
                {this.renderInput({
                  name: "annual_leave",
                  label: "Training Type",
                  type: "number",
                })}
              </Col>
              <Col md={6} sm={12} xs={12}>
                {this.renderSelect({
                  name: "leave_type",
                  label: "Leave Type",
                  options: ["Leave Type 1", "Leave Type 2"],
                })}
              </Col>

              <Col md={6} sm={12} xs={12}>
                {this.renderInput({
                  name: "leave_from_date",
                  label: "Leave From Date",
                  type: "date",
                })}
              </Col>
              <Col md={6} sm={12} xs={12}>
                {this.renderInput({
                  name: "leave_to_date",
                  label: "Leave To Date",
                  type: "date",
                })}
              </Col>
              <Col md={6} sm={12} xs={12}>
                {this.renderInput({
                  name: "leave_balance",
                  label: "Leave Balance",
                  type: "number",
                })}
              </Col>
              <Col md={6} sm={12} xs={12}>
                {this.renderInput({
                  name: "leave_request_date",
                  label: "Leave Request Date",
                  type: "date",
                })}
              </Col>

              <Col md={12} sm={12} xs={12}>
                {this.renderInput({
                  name: "remarks",
                  label: "Remarks",
                  type: "textarea",
                })}
              </Col>
            </Row>
            <CardFooter align="center">{this.renderButton("Save")}</CardFooter>
          </Form>
        </CardBody>
      </Card>
    );
  }
}
export default LeaveBalanceAdd;
