import React from "react";
import ParentForm from "../../../common/form";
import Joi from "joi-browser";
import {
  Card,
  CardBody,

  CardFooter,
  Form,
  Row,
  Col,
} from "reactstrap";

class ConsignmentAdd extends ParentForm {
  constructor(props) {
    super(props);
    this.initialState = {
      data: {
        id: "",
        employee_number: "",
        employee_name: "",
        worked_position: "",
        organization: "",
        start_date: "",
        end_date: "",
        duration: "",
        city: "",
        region: "",
        remarks: "",
      },
      errors: {},
    };
    this.state = this.initialState;
    this.schema = {
      id: Joi.string().allow("").optional(),
      employee_number: Joi.string()
        .optional()
        .allow("")
        .label("Employee Number"),
      employee_name: Joi.string().optional().allow("").label("Employee Name"),
      organization: Joi.string().required().label("Organization"),
      worked_position: Joi.string().required().label("Position"),
      start_date: Joi.string().required().label("Start Date"),
      end_date: Joi.string().required().label("End Date"),
      duration: Joi.string().required().label("Duration"),
      city: Joi.string().required().label("City"),
      region: Joi.string().required().label("Region"),
      remarks: Joi.string().allow("").optional(),
    };
  }

  populateState(data) {
    const updatedState = {
      ...this.state,
      data: {
        id: data.id ? data.id : "",
        employee_number: data.employee_number ? data.employee_number : "",
        employee_name: data.employee_name ? data.employee_name : "",
        organization: data.organization ? data.organization : "",
        worked_position: data.worked_position ? data.worked_position : "",
        start_date: data.start_date ? data.start_date : "",
        end_date: data.end_date ? data.end_date : "",
        duration: data.duration ? data.duration : "",
        city: data.city ? data.city : "",
        region: data.region ? data.region : "",
        remarks: data.remarks ? data.remarks : "",
      },
      lockUpdate: true,
    };
    this.setState(updatedState);
  }

  componentDidUpdate() {
    if ((this.props.isEdit || this.props.isView) && !this.state.lockUpdate) {
      this.populateState(this.props.data);
    }
  }

  doSubmit() {
    this.props.submit(this.state.data);
  }

  render() {
    return (
      <Card className="border-0">
        <CardBody>
          <Form onSubmit={this.handleSubmit}>
            <Row>
              <Col md={6} sm={12} xs={12}>
                {this.renderInput({
                  name: "organization",
                  label: "Organization",
                })}
              </Col>
              <Col md={6} sm={12} xs={12}>
                {this.renderInput({
                  name: "worked_position",
                  label: "Position",
                })}
              </Col>
              <Col md={4} sm={12} xs={12}>
                {this.renderInput({
                  name: "start_date",
                  label: "Start Date",
                  type: "date",
                })}
              </Col>
              <Col md={4} sm={12} xs={12}>
                {this.renderInput({
                  name: "end_date",
                  label: "End Date",
                  type: "date",
                })}
              </Col>
              <Col md={4} sm={12} xs={12}>
                {this.renderInput({
                  name: "duration",
                  label: "Duration",
                })}
              </Col>
              <Col md={6} sm={12} xs={12}>
                {this.renderSelect({
                  name: "region",
                  label: "Region",
                  options: ["Region One", "Region 2"],
                })}
              </Col>
              <Col md={6} sm={12} xs={12}>
                {this.renderSelect({
                  name: "city",
                  label: "City",
                  options: ["City One", "City 2"],
                })}
              </Col>

              <Col md={12} sm={12} xs={12}>
                {this.renderInput({
                  name: "remarks",
                  label: "Remarks",
                  type: "textarea",
                })}
              </Col>
            </Row>
            <CardFooter align="center">{this.renderButton("Save")}</CardFooter>
          </Form>
        </CardBody>
      </Card>
    );
  }
}
export default ConsignmentAdd;
