import React, { useReducer, useEffect } from "react";
import Page from "../../../../components/Page";
import { Card, CardBody, Button, Col } from "reactstrap";
import CommonModals from "../../../../components/CommonModal";
import CertificationAdd from "./CertificationAdd";
import { MdRemoveRedEye, MdEdit, MdDelete } from "react-icons/md";
import { reverse } from "../../../../helpers/reverse";
import CustomTable from "../../../common/table";
import { _toggle, initialState, reducer } from "../../../common/ModalOptions";
import { getDateFormat } from "../../../../helpers/date";

const Certification = ({
  doneAdd,
  addCertification,
  doneEdit,
  editCertification,
  deleteCertification,
  certifications,
}) => {
  const [state, dispatch] = useReducer(reducer, initialState);
  const columns = [
    { path: "employee_number", label: "Employee Number" },
    { path: "certification_type", label: "Certification Type" },
    { path: "education_type", label: "Education Type" },
    { path: "start_date", label: "Start Date" },
    { path: "end_date", label: "End Date" },
    { path: "duration", label: "Duration" },
    { path: "updated_at", label: "Last Modified Date" },
    { path: "remarks", label: "Remarks" },

    {
      key: "view",
      content: (certifications) => (
        <>
          <Button
            className="m-1"
            size="sm"
            outline
            color="info"
            onClick={() => {
              _toggle(
                {
                  type: "VIEW",
                  Component: CertificationAdd,
                  data: certifications,
                  title: "View Certification",
                },
                dispatch
              );
            }}
          >
            <MdRemoveRedEye />
          </Button>
          <Button
            className="m-1"
            size="sm"
            outline
            color="warning"
            onClick={() => {
              _toggle(
                {
                  type: "EDIT",
                  Component: CertificationAdd,
                  submit: editCertification,
                  data: certifications,
                  title: "Edit Certification",
                },
                dispatch
              );
            }}
          >
            <MdEdit />
          </Button>
          <Button
            className="m-1"
            size="sm"
            outline
            color="danger"
            onClick={() => {
              _toggle(
                {
                  type: "DELETE",
                  deleteOptions: {
                    okCallback: okDelete,
                    title: "Are you sure?",
                    id: certifications.id,
                    message: "",
                  },
                },
                dispatch
              );
            }}
          >
            <MdDelete />
          </Button>
        </>
      ),
    },
  ];

  useEffect(() => {
    if (doneAdd || doneEdit) {
      _toggle({ type: "CLOSE" }, dispatch);
    }
  }, [doneAdd, doneEdit]);

  const okDelete = (id) => {
    deleteCertification(id);
  };

  return (
    <div>
      <Page>
        <CommonModals
          data={state.data}
          openModal={state.openModal}
          component={state.Component}
          toggle={_toggle}
          dispatch={dispatch}
          title={state.title}
        />
        <Col align="right" className="mb-1 pl-3 pr-3">
          <Button
            onClick={() =>
              _toggle(
                {
                  type: "ADD",
                  Component: CertificationAdd,
                  submit: addCertification,
                  title: "New Certification",
                },
                dispatch
              )
            }
            outline
            size="sm"
          >
            New Certification
          </Button>
        </Col>
        <Card className="border-0">
          <CardBody>
            <CustomTable
              title="Certifications"
              columns={columns}
              data={reverse(
                certifications.map((item) => ({
                  ...item,
                  updated_at: getDateFormat(item.updated_at),
                }))
              )}
            />
          </CardBody>
        </Card>
      </Page>
    </div>
  );
};

export default Certification;
