import React, { useReducer, useEffect } from "react";
import Page from "../../../../components/Page";
import { Card, CardBody, Button, Col } from "reactstrap";
import CommonModals from "../../../../components/CommonModal";
import LeaveBalanceAdd from "./LeaveBalanceAdd";
import { MdRemoveRedEye, MdEdit, MdDelete } from "react-icons/md";
import { reverse } from "../../../../helpers/reverse";
import CustomTable from "../../../common/table";
import { _toggle, initialState, reducer } from "../../../common/ModalOptions";
import { getDateFormat } from "../../../../helpers/date";

const LeaveBalance = ({
  doneAdd,
  addLeaveBalance,
  doneEdit,
  editLeaveBalance,
  deleteLeaveBalance,
  leaveBalances,
}) => {
  const [state, dispatch] = useReducer(reducer, initialState);
  const columns = [
    { path: "employee_number", label: "Employee Number" },
    { path: "annual_leave", label: "Annual Leave" },
    { path: "leave_type", label: "Leave Type" },
    { path: "leave_request_date", label: "Leave Request Date" },
    { path: "leave_from_date", label: "Leave From Date" },
    { path: "leave_to_date", label: "Leave To Date" },
    { path: "leave_balance", label: "Leave Balance" },
    { path: "duration", label: "Duration" },
    { path: "updated_at", label: "Last Modified Date" },
    { path: "remarks", label: "Remarks" },

    {
      key: "view",
      content: (leaveBalances) => (
        <>
          <Button
            className="m-1"
            size="sm"
            outline
            color="info"
            onClick={() => {
              _toggle(
                {
                  type: "VIEW",
                  Component: LeaveBalanceAdd,
                  data: leaveBalances,
                  title: "View LeaveBalance",
                },
                dispatch
              );
            }}
          >
            <MdRemoveRedEye />
          </Button>
          <Button
            className="m-1"
            size="sm"
            outline
            color="warning"
            onClick={() => {
              _toggle(
                {
                  type: "EDIT",
                  Component: LeaveBalanceAdd,
                  submit: editLeaveBalance,
                  data: leaveBalances,
                  title: "Edit LeaveBalance",
                },
                dispatch
              );
            }}
          >
            <MdEdit />
          </Button>
          <Button
            className="m-1"
            size="sm"
            outline
            color="danger"
            onClick={() => {
              _toggle(
                {
                  type: "DELETE",
                  deleteOptions: {
                    okCallback: okDelete,
                    title: "Are you sure?",
                    id: leaveBalances.id,
                    message: "",
                  },
                },
                dispatch
              );
            }}
          >
            <MdDelete />
          </Button>
        </>
      ),
    },
  ];

  useEffect(() => {
    if (doneAdd || doneEdit) {
      _toggle({ type: "CLOSE" }, dispatch);
    }
  }, [doneAdd, doneEdit]);

  const okDelete = (id) => {
    deleteLeaveBalance(id);
  };

  return (
    <div>
      <Page>
        <CommonModals
          data={state.data}
          openModal={state.openModal}
          component={state.Component}
          toggle={_toggle}
          dispatch={dispatch}
          title={state.title}
        />
        <Col align="right" className="mb-1 pl-3 pr-3">
          <Button
            onClick={() =>
              _toggle(
                {
                  type: "ADD",
                  Component: LeaveBalanceAdd,
                  submit: addLeaveBalance,
                  title: "New Leave Balance",
                },
                dispatch
              )
            }
            outline
            size="sm"
          >
            New Leave Balance
          </Button>
        </Col>
        <Card className="border-0">
          <CardBody>
            <CustomTable
              title="Leave Balances"
              columns={columns}
              data={reverse(
                leaveBalances.map((item) => ({
                  ...item,
                  updated_at: getDateFormat(item.updated_at),
                }))
              )}
            />
          </CardBody>
        </Card>
      </Page>
    </div>
  );
};

export default LeaveBalance;
