import React from "react";
import ParentForm from "../../../common/form";
import Joi from "joi-browser";
import {
  Card,
  CardBody,
  CardFooter,
  Form,
  Row,
  Col,
} from "reactstrap";

class ConsignmentAdd extends ParentForm {
  constructor(props) {
    super(props);
    this.initialState = {
      data: {
        id: "",
        employee_number: "",
        employee_name: "",
        loan_item: "",
        loan_date: "",
        requested_date: "",
        expected_return_date: "",
        actual_return_date: "",
        remarks: "",
      },
      errors: {},
    };
    this.state = this.initialState;
    this.schema = {
      id: Joi.string().allow("").optional(),
      employee_number: Joi.string()
        .optional()
        .allow("")
        .label("Employee Number"),
      employee_name: Joi.string().optional().allow("").label("Employee Name"),
      loan_item: Joi.string().required().label("Loan Item"),
      loan_date: Joi.string().required().label("Loan Date"),
      requested_date: Joi.string().required().label("Requested Date"),
      expected_return_date: Joi.string()
        .required()
        .label("Expected Return Date"),
      actual_return_date: Joi.string().required().label("Actual Return Date"),
      remarks: Joi.string().allow("").optional(),
    };
  }

  populateState(data) {
    const updatedState = {
      ...this.state,
      data: {
        id: data.id ? data.id : "",
        employee_number: data.employee_number ? data.employee_number : "",
        employee_name: data.employee_name ? data.employee_name : "",
        loan_item: data.loan_item ? data.loan_item : "",
        loan_date: data.loan_date ? data.loan_date : "",
        requested_date: data.requested_date ? data.requested_date : "",
        expected_return_date: data.expected_return_date
          ? data.expected_return_date
          : "",
        actual_return_date: data.actual_return_date
          ? data.actual_return_date
          : "",
        remarks: data.remarks ? data.remarks : "",
      },
      lockUpdate: true,
    };
    this.setState(updatedState);
  }

  componentDidUpdate() {
    if ((this.props.isEdit || this.props.isView) && !this.state.lockUpdate) {
      this.populateState(this.props.data);
    }
  }

  doSubmit() {
    this.props.submit(this.state.data);
  }

  render() {
    return (
      <Card className="border-0">
        <CardBody>
          <Form onSubmit={this.handleSubmit}>
            <Row>
              <Col md={4} sm={12} xs={12}>
                {this.renderSelect({
                  name: "loan_item",
                  label: "Loan Item",
                  options: ["Loan Item One 1", "Loan Item One 2"],
                })}
              </Col>
              <Col md={4} sm={12} xs={12}>
                {this.renderInput({
                  name: "loan_date",
                  label: "Loan Date",
                  type: "date",
                })}
              </Col>
              <Col md={4} sm={12} xs={12}>
                {this.renderInput({
                  name: "requested_date",
                  label: "Requested Date",
                  type: "date",
                })}
              </Col>
              <Col md={12} sm={12} xs={12}>
                {this.renderInput({
                  name: "expected_return_date",
                  label: "Expected Return Date",
                  type: "date",
                })}
              </Col>
              <Col md={12} sm={12} xs={12}>
                {this.renderInput({
                  name: "actual_return_date",
                  label: "Actual Return Date",
                  type: "date",
                })}
              </Col>

              <Col md={12} sm={12} xs={12}>
                {this.renderInput({
                  name: "remarks",
                  label: "Remarks",
                  type: "textarea",
                })}
              </Col>
            </Row>
            <CardFooter align="center">{this.renderButton("Save")}</CardFooter>
          </Form>
        </CardBody>
      </Card>
    );
  }
}
export default ConsignmentAdd;
