import React from "react";
import ParentForm from "../../../common/form";
import Joi from "joi-browser";
import {
  Card,
  CardBody,
  
  CardFooter,
  Form,
  Row,
  Col,
} from "reactstrap";

class PerformanceAdd extends ParentForm {
  constructor(props) {
    super(props);
    this.initialState = {
      data: {
        id: "",
        employee_number: "",
        employee_name: "",
        goal_type: "",
        evolution_result: "",
        evaluation_date: "",
        remarks: "",
      },
      errors: {},
    };
    this.state = this.initialState;
    this.schema = {
      id: Joi.string().allow("").optional(),
      employee_number: Joi.string()
        .optional()
        .allow("")
        .label("Employee Number"),
      employee_name: Joi.string().optional().allow("").label("Employee Name"),
      goal_type: Joi.string().required().label("Illness Type"),
      evolution_result: Joi.string().required().label("Body Part"),
      evaluation_date: Joi.string().required().label("Incident Date"),
      remarks: Joi.string().allow("").optional(),
    };
  }

  populateState(data) {
    const updatedState = {
      ...this.state,
      data: {
        id: data.id ? data.id : "",
        employee_number: data.employee_number ? data.employee_number : "",
        employee_name: data.employee_name ? data.employee_name : "",
        goal_type: data.goal_type ? data.goal_type : "",
        evolution_result: data.evolution_result ? data.evolution_result : "",
        evaluation_date: data.evaluation_date ? data.evaluation_date : "",
        remarks: data.remarks ? data.remarks : "",
      },
      lockUpdate: true,
    };
    this.setState(updatedState);
  }

  componentDidUpdate() {
    if ((this.props.isEdit || this.props.isView) && !this.state.lockUpdate) {
      this.populateState(this.props.data);
    }
  }

  doSubmit() {
    this.props.submit(this.state.data);
  }

  render() {
    return (
      <Card className="border-0">
        <CardBody>
          <Form onSubmit={this.handleSubmit}>
            <Row>
              <Col md={12} sm={12} xs={12}>
                {this.renderSelect({
                  name: "goal_type",
                  label: "Goal Type",
                  options: ["Goal 1", "Goal 2"],
                })}
              </Col>
              <Col md={12} sm={12} xs={12}>
                {this.renderInput({
                  name: "evolution_result",
                  label: "Evolution Result",
                  type: "textarea",
                })}
              </Col>
              <Col md={12} sm={12} xs={12}>
                {this.renderInput({
                  name: "evaluation_date",
                  label: "Evaluation Date",
                  type: "date",
                })}
              </Col>

              <Col md={12} sm={12} xs={12}>
                {this.renderInput({
                  name: "remarks",
                  label: "Remarks",
                  type: "textarea",
                })}
              </Col>
            </Row>
            <CardFooter align="center">{this.renderButton("Save")}</CardFooter>
          </Form>
        </CardBody>
      </Card>
    );
  }
}
export default PerformanceAdd;
