import React from "react";
import ParentForm from "../../../common/form";
import Joi from "joi-browser";
import {
  Card,
  CardBody,
  CardFooter,
  Form,
  Row,
  Col,
} from "reactstrap";

class CertificationTypeAdd extends ParentForm {
  constructor(props) {
    super(props);
    this.initialState = {
      data: {
        id: "",
        employee_number: "",
        certification_type: "",
        education_type: "",
        start_date: "",
        end_date: "",
        duration: "",
        remarks: "",
      },
      errors: {},
    };
    this.state = this.initialState;
    this.schema = {
      id: Joi.string().allow("").optional(),
      employee_number: Joi.string()
        .optional()
        .allow("")
        .label("Employee Number"),
      certification_type: Joi.string().required().label("Certification Type"),
      education_type: Joi.string().required().label("Education Type"),
      start_date: Joi.string().required().label("Start Date"),
      end_date: Joi.string().required().label("End Date"),
      duration: Joi.string().required().label("Duration"),
      remarks: Joi.string().allow("").optional(),
    };
  }

  populateState(data) {
    const updatedState = {
      ...this.state,
      data: {
        id: data.id ? data.id : "",
        employee_number: data.employee_number ? data.employee_number : "",
        certification_type: data.certification_type
          ? data.certification_type
          : "",
        education_type: data.education_type ? data.education_type : "",
        start_date: data.start_date ? data.start_date : "",
        end_date: data.end_date ? data.end_date : "",
        duration: data.duration ? data.duration : "",
        remarks: data.remarks ? data.remarks : "",
      },
      lockUpdate: true,
    };
    this.setState(updatedState);
  }

  componentDidUpdate() {
    if ((this.props.isEdit || this.props.isView) && !this.state.lockUpdate) {
      this.populateState(this.props.data);
    }
  }

  doSubmit() {
    this.props.submit(this.state.data);
  }

  render() {
    return (
      <Card className="border-0">
        <CardBody>
          <Form onSubmit={this.handleSubmit}>
            <Row>
              <Col md={6} sm={12} xs={12}>
                {this.renderSelect({
                  name: "certification_type",
                  label: "Certification Type",
                  options: ["Certification 1", "Certification 2"],
                })}
              </Col>
              <Col md={6} sm={12} xs={12}>
                {this.renderSelect({
                  name: "education_type",
                  label: "Education Type",
                  options: ["Education 1", "Education 2"],
                })}
              </Col>
              <Col md={4} sm={12} xs={12}>
                {this.renderInput({
                  name: "start_date",
                  label: "Start Date",
                  type: "date",
                })}
              </Col>
              <Col md={4} sm={12} xs={12}>
                {this.renderInput({
                  name: "end_date",
                  label: "End Date",
                  type: "date",
                })}
              </Col>
              <Col md={4} sm={12} xs={12}>
                {this.renderInput({
                  name: "duration",
                  label: "Duration",
                  type: "number",
                })}
              </Col>

              <Col md={12} sm={12} xs={12}>
                {this.renderInput({
                  name: "remarks",
                  label: "Remarks",
                  type: "textarea",
                })}
              </Col>
            </Row>
            <CardFooter align="center">{this.renderButton("Save")}</CardFooter>
          </Form>
        </CardBody>
      </Card>
    );
  }
}
export default CertificationTypeAdd;
