import React from "react";
import ParentForm from "../../../common/form";
import Joi from "joi-browser";
import {
  Card,
  CardBody,

  CardFooter,
  Form,
  Row,
  Col,
} from "reactstrap";

class PerformanceAdd extends ParentForm {
  constructor(props) {
    super(props);
    this.initialState = {
      data: {
        id: "",
        employee_number: "",
        employee_name: "",
        start_date: "",
        end_date: "",
        total_absence_days: "",
        total_absence_hours: "",
        total_absence: "",
        remarks: "",
      },
      errors: {},
    };
    this.state = this.initialState;
    this.schema = {
      id: Joi.string().allow("").optional(),
      employee_number: Joi.string()
        .optional()
        .allow("")
        .label("Employee Number"),
      employee_name: Joi.string().optional().allow("").label("Employee Name"),
      start_date: Joi.string().required().label("Start Date"),
      end_date: Joi.string().required().label("End Date"),
      total_absence_days: Joi.string().required().label("Total Absence Days"),
      total_absence_hours: Joi.string().required().label("Total Absence Hours"),
      total_absence: Joi.string().required().label("Total Absence"),
      remarks: Joi.string().allow("").optional(),
    };
  }

  populateState(data) {
    const updatedState = {
      ...this.state,
      data: {
        id: data.id ? data.id : "",
        employee_number: data.employee_number ? data.employee_number : "",
        employee_name: data.employee_name ? data.employee_name : "",
        start_date: data.start_date ? data.start_date : "",
        end_date: data.end_date ? data.end_date : "",
        total_absence_hours: data.total_absence_hours
          ? data.total_absence_hours
          : "",
        total_absence: data.total_absence ? data.total_absence : "",
        total_absence_days: data.total_absence_days
          ? data.total_absence_days
          : "",
        remarks: data.remarks ? data.remarks : "",
      },
      lockUpdate: true,
    };
    this.setState(updatedState);
  }

  componentDidUpdate() {
    if ((this.props.isEdit || this.props.isView) && !this.state.lockUpdate) {
      this.populateState(this.props.data);
    }
  }

  doSubmit() {
    this.props.submit(this.state.data);
  }

  render() {
    return (
      <Card className="border-0">
        <CardBody>
          <Form onSubmit={this.handleSubmit}>
            <Row>
              <Col md={6} sm={12} xs={12}>
                {this.renderInput({
                  name: "start_date",
                  label: "Start Date",
                  type: "date",
                })}
              </Col>
              <Col md={6} sm={12} xs={12}>
                {this.renderInput({
                  name: "end_date",
                  label: "End Date",
                  type: "date",
                })}
              </Col>
              <Col md={6} sm={12} xs={12}>
                {this.renderInput({
                  name: "total_absence_days",
                  label: "Today Absence Days",
                })}
              </Col>
              <Col md={6} sm={12} xs={12}>
                {this.renderInput({
                  name: "total_absence_hours",
                  label: "Today Absence Hours",
                  type: "number",
                })}
              </Col>
              <Col md={12} sm={12} xs={12}>
                {this.renderInput({
                  name: "total_absence",
                  label: "Today Absence",
                  type: "number",
                })}
              </Col>

              <Col md={12} sm={12} xs={12}>
                {this.renderInput({
                  name: "remarks",
                  label: "Remarks",
                  type: "textarea",
                })}
              </Col>
            </Row>
            <CardFooter align="center">{this.renderButton("Save")}</CardFooter>
          </Form>
        </CardBody>
      </Card>
    );
  }
}
export default PerformanceAdd;
