import React from "react";
import ParentForm from "../../../common/form";
import Joi from "joi-browser";
import {
  Card,
  CardBody,
  CardFooter,
  Form,
  Row,
  Col,
} from "reactstrap";

class TerminationAdd extends ParentForm {
  constructor(props) {
    super(props);
    this.initialState = {
      data: {
        id: "",
        employee_number: "",
        termination_type: "",
        last_working_date: "",
        termination_request_date: "",
        remarks: "",
      },
      errors: {},
    };
    this.state = this.initialState;
    this.schema = {
      id: Joi.string().allow("").optional(),
      employee_number: Joi.string()
        .optional()
        .allow("")
        .label("Employee Number"),
      termination_type: Joi.string()
        .optional()
        .allow("")
        .label("Termination Type"),
      last_working_date: Joi.string().required().label("Last Working Date"),

      termination_request_date: Joi.string()
        .required()
        .label("Termination Request Date"),
      remarks: Joi.string().allow("").optional(),
    };
  }

  populateState(data) {
    const updatedState = {
      ...this.state,
      data: {
        id: data.id ? data.id : "",
        employee_number: data.employee_number ? data.employee_number : "",
        termination_type: data.termination_type ? data.termination_type : "",
        last_working_date: data.last_working_date ? data.last_working_date : "",
        termination_request_date: data.termination_request_date
          ? data.termination_request_date
          : "",
        remarks: data.remarks ? data.remarks : "",
      },
      lockUpdate: true,
    };
    this.setState(updatedState);
  }

  componentDidUpdate() {
    if ((this.props.isEdit || this.props.isView) && !this.state.lockUpdate) {
      this.populateState(this.props.data);
    }
  }

  doSubmit() {
    this.props.submit(this.state.data);
  }

  render() {
    return (
      <Card className="border-0">
        <CardBody>
          <Form onSubmit={this.handleSubmit}>
            <Row>
              <Col md={4} sm={12} xs={12}>
                {this.renderSelect({
                  name: "termination_type",
                  label: "Termination Type Type",
                  options: ["Termination Type 1", "Termination Type 2"],
                })}
              </Col>
              <Col md={4} sm={12} xs={12}>
                {this.renderInput({
                  name: "last_working_date",
                  label: "Last Working Date",
                  type: "date",
                })}
              </Col>

              <Col md={4} sm={12} xs={12}>
                {this.renderInput({
                  name: "termination_request_date",
                  label: "Termination Request Date",
                  type: "date",
                })}
              </Col>

              <Col md={12} sm={12} xs={12}>
                {this.renderInput({
                  name: "remarks",
                  label: "Remarks",
                  type: "textarea",
                })}
              </Col>
            </Row>
            <CardFooter align="center">{this.renderButton("Save")}</CardFooter>
          </Form>
        </CardBody>
      </Card>
    );
  }
}
export default TerminationAdd;
