import React, { useReducer, useEffect } from "react";
import Page from "../../../../components/Page";
import { Card, CardBody, Button, Col } from "reactstrap";
import CommonModals from "../../../../components/CommonModal";
import DisciplinaryActionAdd from "./DisciplinaryActionAdd";
import { MdRemoveRedEye, MdEdit, MdDelete } from "react-icons/md";
import { reverse } from "../../../../helpers/reverse";
import CustomTable from "../../../common/table";
import { _toggle, initialState, reducer } from "../../../common/ModalOptions";
import { getDateFormat } from "../../../../helpers/date";

const DisciplinaryAction = ({
  doneAdd,
  addDisciplinaryAction,
  doneEdit,
  editDisciplinaryAction,
  deleteDisciplinaryAction,
  disciplinaryActions,
}) => {
  const [state, dispatch] = useReducer(reducer, initialState);
  const columns = [
    { path: "employee_number", label: "Employee Number" },
    { path: "disciplinary_action_type", label: "Disciplinary Action Type" },
    { path: "from_date", label: "From Date" },
    { path: "to_date", label: "To Date" },
    { path: "updated_at", label: "Last Modified Date" },
    { path: "remarks", label: "Remarks" },

    {
      key: "view",
      content: (disciplinaryActions) => (
        <>
          <Button
            className="m-1"
            size="sm"
            outline
            color="info"
            onClick={() => {
              _toggle(
                {
                  type: "VIEW",
                  Component: DisciplinaryActionAdd,
                  data: disciplinaryActions,
                  title: "View Disciplinary Action",
                },
                dispatch
              );
            }}
          >
            <MdRemoveRedEye />
          </Button>
          <Button
            className="m-1"
            size="sm"
            outline
            color="warning"
            onClick={() => {
              _toggle(
                {
                  type: "EDIT",
                  Component: DisciplinaryActionAdd,
                  submit: editDisciplinaryAction,
                  data: disciplinaryActions,
                  title: "Edit Disciplinary Action",
                },
                dispatch
              );
            }}
          >
            <MdEdit />
          </Button>
          <Button
            className="m-1"
            size="sm"
            outline
            color="danger"
            onClick={() => {
              _toggle(
                {
                  type: "DELETE",
                  deleteOptions: {
                    okCallback: okDelete,
                    title: "Are you sure?",
                    id: disciplinaryActions.id,
                    message: "",
                  },
                },
                dispatch
              );
            }}
          >
            <MdDelete />
          </Button>
        </>
      ),
    },
  ];

  useEffect(() => {
    if (doneAdd || doneEdit) {
      _toggle({ type: "CLOSE" }, dispatch);
    }
  }, [doneAdd, doneEdit]);

  const okDelete = (id) => {
    deleteDisciplinaryAction(id);
  };

  return (
    <div>
      <Page>
        <CommonModals
          data={state.data}
          openModal={state.openModal}
          component={state.Component}
          toggle={_toggle}
          dispatch={dispatch}
          title={state.title}
        />
        <Col align="right" className="mb-1 pl-3 pr-3">
          <Button
            onClick={() =>
              _toggle(
                {
                  type: "ADD",
                  Component: DisciplinaryActionAdd,
                  submit: addDisciplinaryAction,
                  title: "New Disciplinary Action",
                },
                dispatch
              )
            }
            outline
            size="sm"
          >
            New Disciplinary Action
          </Button>
        </Col>
        <Card className="border-0">
          <CardBody>
            <CustomTable
              title="Disciplinary Action"
              columns={columns}
              data={reverse(
                disciplinaryActions.map((item) => ({
                  ...item,
                  updated_at: getDateFormat(item.updated_at),
                }))
              )}
            />
          </CardBody>
        </Card>
      </Page>
    </div>
  );
};

export default DisciplinaryAction;
